<template>
    <div id="display-bot">
    <div id="textDisplayContainer">
        <span v-text="textDisplay"></span>
    </div>
   </div> 
</template>

<script>
let textNum = 0;

export default {
  data() {
    return {
      textDisplay: `interval`,
      subject: [
        `List of Graphic Design Student Works`,
        `CcIntervalExample`,
        `start-interval`,
        `interval`
      ]
    };
  },
  created() {
    this.changeText();
  },
  methods: {
    changeText: function() {
      setInterval(() => {
        this.textDisplay = this.subject[textNum];
        textNum = textNum + 1;
        if (textNum > 3) {
          textNum = 0;
        }
      }, 2500);
    }
  }
};
</script>

<style>
#display-bot {
  position: absolute;
  height: auto;
  width: 100%;
  top: 300px;
  left: -300px;
  font-size: 32em;
  white-space: nowrap;
  text-align: center;
  justify-content: center;
  mix-blend-mode: difference;
  
}
#display-bot {
  text-align: center;
  color: white;
  font-family:Soyuz;
}
</style>



