<template>
    <div id = "menu">
      <p id="list-please">
please<br> select <br>the <br>method <br>of<br> view<br> you<br> want</p>
        <router-link to="/controllist"><p id="list-link" v-on:click="emitEvent('displaylist')">Text List</p></router-link>
        <router-link to="/controllist"><p   id="list-link" v-on:click="emitEvent('displaymap')">Image Map</p></router-link>
    </div>
</template>

<script>
export default {
  methods: {
    emitEvent: function(data) {
      this.$socket.emit("controlAction", data);
      console.log("clicked");
    }
  }
};
</script>ß

<style scoped>
#menu {

  
  padding-top:5%;
  display: flex;
  flex-direction: column;

}
a {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

#list-please{
  mix-blend-mode:difference;
color:white;
  font-size: 1em;
  font-weight: 700;
  text-align: center;
}
#list-link{
  color:black;
  font-size: 2.5em;
  font-weight: 700;
}
</style>



