<template>
<div id="display-top">
    <div id="firstLine" class="textWraper">
        <span v-text="subject"></span>
    </div>
    <div id = "distort">
    <div id="secondLine" class="textWraper">
        
        <span id ="please" v-text="please[0]"></span>
        <span id="urls" v-text="url"></span> 
        <span id ="please" v-text="please[1]"></span>   
        <span id="smartPhone">Smartphone </span> 
        <span id ="please" v-text="please[2]"></span>       
    </div>
    </div>
    </div>  
</template>

<script>
export default {
  data() {
    return {
      subject: "List of Graphic Design Student Works",
      please: ["Enter to “","” by your "," and control the screen."],
      url: "orgd.list.kr "
    };
  },methods:{
    secondLineAnimate:function(){
                     var second= TweenMax.to("#secondLine", 1, {ease: Linear.easeNone,css:{left:"-130%"}, repeat: -1 });
                      var currentDuration = second.duration();
                      second.duration(18); 
    }
  },mounted(){
  this.secondLineAnimate();
  }
};
</script>

<style>
#display-top {
    mix-blend-mode: difference;
color:white;
  z-index:100;
  width: 100%;
  overflow: hidden;
  
}
.textWraper {
  overflow:hidden;
  

}
#display-top span {
  
  white-space: nowrap;
  font-size: 5em;
  text-overflow: clip;
  font-family:varfont;
  font-variation-settings: 'wght' 120, 'ital' 0;
}
#firstLine {
  width:1450px;
height:102px;
font-style:bold;
  margin-top: 15px;
  padding-left:15px;
  padding-top:3.5px;
  margin-left: 20px;
  border:solid white 2px;
}
#firstLine span{
  /* filter: blur(5px);  */
}
#urls {
  font-style: italic;
}
#distort{
  height:500px;
width:100%;
  transform-origin: 0px -0px 0px;
/* transform: matrix3d(0.893027, -0.0217, 0, -0.000976, 
                    0.027939, 0.957911, 0, 0.000176, 
                    0, 0, 1, 0, 
                    100.008415, 13.009014, 0, 1); */
}
#secondLine {
  position: absolute;
  white-space: nowrap;
  left:100%;
  margin-top:10px;
  }
#please {
   /*filter: blur(2px);*/

}
#smartPhone {
  
  /* filter: blur(3px); */
  padding-right: 5px;
    mix-blend-mode: difference;
}

</style>



