<template>
<div id="mainCon">

      <leftTop></leftTop>

    <div id="contents">
      <leftBot></leftBot>
    </div>
</div>
</template>

<script>
import leftBot from "./control/leftBot.vue";
import leftTop from "./control/leftTop.vue";
export default {
  name: "ControlPage",
  components: {
    leftBot: leftBot,
    leftTop: leftTop
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#mainCon {
  height: 100%;
}
#contents {
  padding-top: 8%;
}
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



